import React from 'react'
import { graphql, useStaticQuery } from 'gatsby'
import Layout from '../components/layout'
import SEO from '../components/seo'
import Img from 'gatsby-image'

const IndexPage = () => {
    const data = useStaticQuery(graphql`
        query {
            file(relativePath: { eq: "about-0001.png" }) {
                childImageSharp {
                    fluid(maxWidth: 500) {
                        ...GatsbyImageSharpFluid
                    }
                }
            }
        }
    `)

    return (
        <Layout>
            <SEO title="About" />

            <div className="sm:w-3/4">

                <div className="flex">
                    <div className="sm:w-1/2">
                        <Img fluid={data.file.childImageSharp.fluid} />
                    </div>

                    <div className="sm:w-1/2 sm:pl-4 self-end">
                        <div className="my-12 text-H1 font-bold">How does a science-loving book nerd end up a UX designer?</div>

                        <div className="sm:hidden">
                            <Img fluid={data.file.childImageSharp.fluid} />
                        </div>

                        <div className="my-4 text-H2 font-bold text-pumpkin">tl;dr</div>
                        <p className="my-4">I’ve spent more than seven years in the startup space on the business side of the house at companies like Slack and Blend, but recently pivoted to pursue a career in UX design.</p>
                    </div>
                </div>

                <div className="sm:clear-left my-4 text-H2 font-bold text-pumpkin">Full Story</div>
                <p className="my-4">I grew up in a tiny town in California. </p>
                <p className="my-4">When I was a kid, I had a lot of questions about the way the world worked — so many questions, in fact, that often, my parents just didn’t have the answers. So I fed my curiosity by reading about the world beyond my tiny town. And the first chance I had, I made the move to San Francisco to pursue a degree in biology at San Francisco State.</p>

                <div className="my-4 text-B1 font-bold">A tale of science and startups</div>
                <p className="my-4">Studying biology meant I could finally get answers to some of the questions that kept me up at night. I threw myself into biology coursework and derived an obscene amount of joy from planning research projects and experiments.</p>
                <p className="my-4">After college, my first job was at a small startup in San Francisco. At the time, it occurred to me that the world of startups wasn’t unlike the world of science: Everyone was building products that were, essentially, ongoing experiments. Product people, as I learned, were the scientists that conducted the experiments so that they could ultimately build a more valuable product offering. For more than seven years, the moving pieces of startups fascinated me, and fueled my career as a facilities professional at companies like Slack and Blend.</p>

                <div className="my-4 text-B1 font-bold">Discovering my love of product</div>
                <p className="my-4">I’ve always asked a lot of questions of the world. But after nearly a decade in facilities, I started asking questions of myself. And what I discovered was that it was time for a challenge. Instead of merely observing the product experiments I’d seen at startups over the years, I wanted to be a part of them — specifically, as a UX designer. So, much like I did when I left home to explore the broader world beyond my hometown many years ago, I’m beginning my journey into the world of product and UX design. I’m excited about what the future holds!</p>

                <div className="my-4 text-H2 font-bold text-pumpkin">Additional tidbits...</div>
                <ul className="list-disc ml-6">
                    <li className="my-4">The books I gravitate towards tend to be science fiction, fantasy, and mystery. I adore classics.</li>
                    <li className="my-4">I was a drama kid in school but never got over my crippling stage fright. I still love theater.</li>
                    <li className="my-4">I am a mom to 2 stepsons and 1 extremely adorable little girl named Beatriz.</li>
                    <li className="my-4">I need nature like a cactus needs the sun, I recently started a patio garden that I’m moderately proud of.</li>
                </ul>

                <div className="flex justify-center">
                    <a href="/Lynn Baxter Résumé.pdf"
                        className="text-btn py-1 px-3 my-8 border border-current text-pumpkin rounded inline-flex items-center justify-center">
                        <span>Download my résumé</span>
                        <svg className="w-2 h-3 ml-2" viewBox="0 0 7 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path
                                d="M5.82386e-05 10.7629L0.716129 11.5L6.37866 6.00018L0.71613 0.500001L5.90713e-05 1.23725L4.90306 6.00018L5.82386e-05 10.7629Z"
                                fill="#DF5F02" />
                        </svg>
                    </a>
                </div>

            </div>
        </Layout >
    )
}
export default IndexPage
